<template>

    <div>
        <swiper :options="swiperOption">
            <swiper-slide>
                <img class="pic" src="../assets/1.png"/>
            </swiper-slide>
            <swiper-slide>
                <img class="pic" src="../assets/2.png"/>
            </swiper-slide>
            <swiper-slide>
                <img class="pic" src="../assets/3.png"/>
            </swiper-slide>
        </swiper>
    </div>
</template>

<script>
    import 'swiper/dist/css/swiper.css'

    import {swiper, swiperSlide} from 'vue-awesome-swiper'

    export default {
        name: "SwiperPic",
        components: {
            swiper,
            swiperSlide
        },
        data() {
            return {
                swiperOption: {
                    autoplay: {
                        delay: 2500,
                        disableOnInteraction: false
                    }
                }
            }
        }
    }
</script>

<style scoped>
    .pic {
        width: 100%;
    }
</style>